import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import * as rootSelects from '../../store/reducers';
import * as rootActions from '../../store/actions';

import {
	Panel,
	PanelHeader,
	HeaderButton,
	platform,
	IOS,
	Footer,
	PullToRefresh,
	Group,
	List,
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import User from '../../components/User';

const FriendsContainer = (props) => {
	let offsetFriends = 100;
	const {id, go, users, dispatch,} = props;
	const [isFetching, setIsFetching] = useState(false);
	const osname = platform();
	const {
		frendsActions: {updateFriends, fetchedFriends}
	} = rootActions;

	useEffect(() => {
		window.addEventListener('scroll', handleScroll);
		dispatch(fetchedFriends(setFetching));
	}, []);

	const onRefresh = () => {
		setIsFetching(true);
		dispatch(fetchedFriends(setFetching));
	};

	const setFetching = () => setIsFetching(false);

	const handleScroll = (e) => {
		const scrollHeight = document.documentElement.scrollHeight;
		const clientHeight = document.documentElement.clientHeight;
		const scrollY = window.scrollY + 1;
		if (scrollY >= scrollHeight - clientHeight) {
			dispatch(updateFriends(offsetFriends));
			offsetFriends += 100;
		}
	}

	return(
		<Panel id={id}>
			<PanelHeader
				left={<HeaderButton onClick={go} data-to="home">
					{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
				</HeaderButton>}
			>
				Друзья
			</PanelHeader>

			<PullToRefresh
				onScroll={handleScroll}
				onRefresh={onRefresh}
				isFetching={isFetching}>
				{!!users.length && <Group title="">
					{users.map(user => (
						<List key={user.id}>
							<User fetchedUser={user} go={go} />
							<span></span>
						</List>
					))}
				</Group>}
				<Footer>{users.length} друзей</Footer>
			</PullToRefresh>
		</Panel>
	);
}

FriendsContainer.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => {
	const {
		selectFriends: {getFriends}
	} = rootSelects;

	return {
		users: getFriends(state)
	};
};

export default connect(mapStateToProps, null)(FriendsContainer);
