import { SET_USER } from './actionType';

const userInitialState = {
	photo_200: 'https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg',
	first_name: 'Вася',
	last_name: 'Васечкин',
	city: { title: 'New York' }
};

export default function user (state = userInitialState, action = {}) {
	switch (action.type) {
		case SET_USER:
			return {
				...state,
				...action.payload
			};
		default:
			return state;
	};
};

export const getUser = (state) => {
	return state.user;
};