import React, {useEffect} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import * as rootSelects from '../store/reducers';
import * as rootActions from '../store/actions';

import {Panel, List, Group, PanelHeader, Cell} from '@vkontakte/vkui';

import User from '../components/User';

const useFetchedUser = (dispatch) => {
	useEffect(() => {
		const {
			userActions: {fetchedUser}
		} = rootActions;

		dispatch(fetchedUser());
	}, []);
};

const Home = (props) => {
	const {id, go, fetchedUser, dispatch} = props;
	useFetchedUser(dispatch);

	return(
		<Panel id={id}>
			<PanelHeader>Главная</PanelHeader>

			<Group title="">
				<User size={48} fetchedUser={fetchedUser} go={go} />
			</Group>

			<Group title="Переходы">
				<List>
					<Cell expandable onClick={go} data-to='friends'>Друзья</Cell>
				</List>
			</Group>
		</Panel>
	);
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => {
	const {
		selectUser: {getUser}
	} = rootSelects;

	return {
		fetchedUser: getUser(state)
	};
};

export default connect(mapStateToProps, null)(Home);
