import React from 'react';
import PropTypes from 'prop-types';
import {Cell, Avatar} from '@vkontakte/vkui';

const  User = ({size=40, fetchedUser}) => {
	const {
		id,
		photo_50,
		photo_100,
		photo_200,
		photo_200_orig,
		city = {title: ''},
		first_name,
		last_name,
		bdate
	} = fetchedUser;
	const photo = photo_200_orig || photo_200 || photo_100 || photo_50;
	return (
		<Cell
			key={id}
			before={<Avatar size={size} src={photo} />}
			size="l"
			description={city.title}
			bottomContent={bdate && `День рождения: ${bdate}`}
		>
			{`${first_name} ${last_name}`}
		</Cell>
	);
}

User.propTypes = {
	fetchedUser: PropTypes.shape({
		id: PropTypes.number,
		size: PropTypes.number,
		photo_50: PropTypes.string,
		photo_100: PropTypes.string,
		photo_200: PropTypes.string,
		photo_200_orig: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		bdate: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		})
	})
};

export default User;